@import url("https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap");
/* bar container */
.navbar {
  background-color: #1f2056;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 20;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90px;
  max-width: 1400px;
  position: relative;
}
/* hover item */
.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 1rem;
  flex-grow: 1;
}

.nav-links {
  width: 120px;
  margin-top: auto;
  margin-right: auto;
  margin-bottom: auto;
  margin-left: 0;
  padding-top: 5px;
  padding-right: 10px;
  padding-bottom: 5px;
  padding-left: 10px;
  text-align: center;
  border-radius: 50px;
  text-transform: uppercase;
  border: 1px solid #1d4e88;
  color: #ffffff;
  text-decoration: none;
  padding: 1rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  border-radius: 50px;
  transition: border-width 0.7s ease, background-color 0.9s ease;
}
/* item hover */
.nav-item:hover:after {
  width: 100%;
  background: #ff4040;
}

/* logo */
.nav-logo {
  color: #21f53d;
  align-items: center;
  margin-right: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 3rem;
  display: flex;
  align-items: center;
  justify-items: center;
}

.nav-logo .icon {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  margin-left: 16px;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1pxsolid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  body {
    font-family: "Noto Sans", sans-serif;
    font-style: normal;
    font-weight: 200;
  }
}
